@import url(https://fonts.googleapis.com/css?family=Permanent+Marker|Roboto|Roboto+Condensed:300&display=swap);
html, body, #root {
  margin: 0;
  padding: 0;
  width: 100%; }

body {
  font-size: 1rem;
  font-family: Roboto, sans-serif;
  background-color: #f8f8f8;
  color: #999999; }

.App {
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center; }

.top-bar {
  flex: 1 1;
  width: 100%;
  display: flex;
  flex-direction: row; }
  .top-bar a {
    text-decoration: none;
    color: #999999; }
  .top-bar__header {
    margin-left: auto;
    margin-right: auto; }
  .top-bar__menu {
    margin-right: 0;
    margin-left: auto;
    position: absolute;
    right: 1rem;
    top: 2.5rem; }
    .top-bar__menu a {
      text-decoration: underline; }
  .top-bar__quant {
    font-family: 'Permanent Marker', cursive;
    color: #4FC3F7; }
  .top-bar small {
    font-size: 0.6em;
    font-style: italic; }

@media (max-width: 600px) {
  .top-bar__menu {
    top: 5rem; } }

